// Inicializar Firebase
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

/**
 * Agrega un registro a una colección en Firestore.
 * @param {string} collectionName - Nombre de la colección.
 * @param {object} data - Datos a guardar.
 * @returns {Promise} - Promesa con el ID del documento creado.
 */
export const addRecord = async (collectionName, data) => {
  if (!collectionName || !data) {
    throw new Error("Colección y datos son requeridos");
  }

  try {
    const docRef = await addDoc(collection(db, collectionName), data);
    return docRef.id;
  } catch (error) {
    console.error("Error al agregar el registro:", error);
    throw error;
  }
};



/**
 * Obtiene todos los registros de una colección en Firestore.
 * @param {string} collectionName - Nombre de la colección.
 * @returns {Promise} - Promesa con un array de documentos.
 */
export const getRecords = async (collectionName = "routes") => {
  try {
    console.log(`Obteniendo registros de la colección ${collectionName}...`);
    const querySnapshot = await getDocs(collection(db, collectionName));
    console.log(`Registros obtenidos: ${querySnapshot.docs.length}`);
    const records = querySnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
    console.log(`Registros procesados: ${records.length}`);
    console.log(`Registros:`, records);
    return records;
  } catch (error) {
    console.error(`Error al obtener los registros de la colección ${collectionName}:`, error);
    throw error;
  }
};

/**
 * Actualiza un registro en Firestore.
 * @param {string} collectionName - Nombre de la colección.
 * @param {string} id - ID del documento a actualizar.
 * @param {object} data - Datos a actualizar.
 * @returns {Promise} - Promesa cuando se completa la actualización.
 */
export const updateRecord = async (collectionName, id, data) => {
  try {
    const docRef = doc(db, collectionName, id);
    await updateDoc(docRef, data);
  } catch (error) {
    console.error("Error al actualizar el registro:", error);
    throw error;
  }
};

/**
 * Elimina un registro de una colección en Firestore.
 * @param {string} collectionName - Nombre de la colección.
 * @param {string} id - ID del documento a eliminar.
 * @returns {Promise} - Promesa cuando se completa la eliminación.
 */
export const deleteRecord = async (collectionName, id) => {
  try {
    const docRef = doc(db, collectionName, id);
    await deleteDoc(docRef);
  } catch (error) {
    console.error("Error al eliminar el registro:", error);
    throw error;
  }
};

// Función para agregar persona a la colección de estado
export const addPersonState = async (nombre, estado, fecha, comentario) => {
  try {
    await addDoc(collection(db, "personasEstado"), {
      nombre,
      estado,
      fecha,
      comentario,
    });
  } catch (error) {
    console.error("Error al agregar persona:", error);
  }
};



export const getFuelRecords = async () => {
  try {
    const records = await getRecords("audit");
    return records;
  } catch (error) {
    console.error("Error al obtener los registros de la colección fuel:", error);
    throw error;
  }
};

export const addFuelRecord = async (data) => {
  try {
    const id = await addRecord("audit", data);
    return id;
  } catch (error) {
    console.error("Error al agregar el registro a la colección fuel:", error);
    throw error;
  }
};