import React, { useState, useEffect } from "react";
import { addRecord, getRecords, updateRecord, deleteRecord } from "../../utils/firebaseService";
import Button from 'react-bootstrap/Button';
import { Modal } from 'react-bootstrap';


const UnitsManagement = () => {
  const [units, setUnits] = useState([]);
  const [newUnit, setNewUnit] = useState({
    number: "",
    plate: "",
    type: "",
    indicator: "",
    capacity: "",
    status: "ACTIVO"
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");
  const [editingUnit, setEditingUnit] = useState(null);
  const [filteredUnits, setFilteredUnits] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [isEditing, setIsEditing] = useState(false);

  useEffect(() => {
    const fetchUnits = async () => {
      const data = await getRecords("units");
      const sortedUnits = data.sort((a, b) => a.number - b.number);
      setUnits(sortedUnits);
    };
    fetchUnits();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewUnit({ ...newUnit, [name]: value });
  };

  const handleEdit = (unit) => {
    setEditingUnit(unit);
    setNewUnit(unit);
    setIsEditing(true);
    setShowModal(true);
  };

  const handleUpdate = async () => {
    setLoading(true);
    try {
      await updateRecord("units", editingUnit.id, newUnit);
      const updatedUnits = units.map(unit => unit.id === editingUnit.id ? newUnit : unit);
      const sortedUnits = updatedUnits.sort((a, b) => a.number - b.number);
      setUnits(sortedUnits);
      setNewUnit({ number: "", plate: "", type: "", indicator: "", capacity: "", status: "ACTIVO" });
      setEditingUnit(null);
      setIsEditing(false);
      setShowModal(false);
      setMessage("Unidad actualizada con éxito.");
    } catch (error) {
      setMessage("Error al actualizar la unidad.");
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async () => {
    setLoading(true);
    try {
      await deleteRecord("units", editingUnit.id);
      const updatedUnits = units.filter(unit => unit.id !== editingUnit.id);
      setUnits(updatedUnits);
      setNewUnit({ number: "", plate: "", type: "", indicator: "", capacity: "", status: "ACTIVO" });
      setEditingUnit(null);
      setIsEditing(false);
      setShowModal(false);
      setMessage("Unidad eliminada con éxito.");
    } catch (error) {
      setMessage("Error al eliminar la unidad.");
    } finally {
      setLoading(false);
    }
  };

  const addUnit = async () => {
    setLoading(true);
    try {
      await addRecord("units", newUnit);
      const newUnitWithId = { ...newUnit, id: Date.now() };
      const updatedUnits = [...units, newUnitWithId];
      const sortedUnits = updatedUnits.sort((a, b) => a.number - b.number);
      setUnits(sortedUnits);
      setNewUnit({ number: "", plate: "", type: "", indicator: "", capacity: "", status: "ACTIVO" });
      setIsEditing(false);
      setShowModal(false);
      setMessage("Unidad agregada con éxito.");
    } catch (error) {
      setMessage("Error al agregar la unidad.");
    } finally {
      setLoading(false);
    }
  };

  const handleFilter = (e) => {
    const estado = e.target.value;
    if (estado === "Todos") {
      setFilteredUnits([]);
    } else {
      const filteredUnits = units.filter(unit => unit.status === estado);
      setFilteredUnits(filteredUnits);
    }
  };

  const activas = units.filter(unit => unit.status === "ACTIVO").length;
  const inactivas = units.filter(unit => unit.status === "INACTIVO").length;
  const enTaller = units.filter(unit => unit.status === "TALLER").length;

  return (
    <div className="container mt-5">
      <h3 className="mt-5">Resesumen de Unidades</h3>
      <div className="row">
        <div className="col-md-4">
          <div className="card bg-success text-white">
            <div className="card-body">
              <h5 className="card-title">Unidades Activas</h5>
              <p className="card-text" style={{ fontSize: 24 }}>{activas}</p>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card bg-warning text-dark">
            <div className="card-body">
              <h5 className="card-title">Unidades en Taller</h5>
              <p className="card-text" style={{ fontSize: 24 }}>{enTaller}</p>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card bg-danger text-white">
            <div className="card-body">
              <h5 className="card-title">Unidades Inactivas</h5>
              <p className="card-text" style={{ fontSize: 24 }}>{inactivas}</p>
            </div>
          </div>
        </div>
      </div>

      <h2 className="text-center mb-4">Gestión de Unidades</h2>

      <Button variant="primary" onClick={() => {
        setIsEditing(false);
        setNewUnit({ number: "", plate: "", type: "", indicator: "", capacity: "", status: "ACTIVO" });
        setShowModal(true);
      }}>
        Agregar Unidad
      </Button>

      {message && <div className="alert alert-info mt-3">{message}</div>}

      <h3 className="mt-5">Unidades Registradas</h3>
      <select
        className="form-control"
        value={filteredUnits.length > 0 ? filteredUnits[0].status : "Todos"}
        onChange={handleFilter}
      >
        <option value="Todos">Todos</option>
        <option value="ACTIVO">ACTIVO</option>
        <option value="INACTIVO">INACTIVO</option>
        <option value="TALLER">TALLER</option>
      </select>
      <table className="table table-striped mt-3">
        <thead>
          <tr>
            <th>Número de Unidad</th>
            <th>Placa</th>
            <th>Tipo de Vehículo</th>
            <th>Kilómetros por Galón</th>
            <th>Capacidad (Q)</th>
            <th>Estado</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {filteredUnits.length > 0 ? filteredUnits.map((unit) => (
            <tr key={unit.id}>
              <td>{unit.number}</td>
              <td>{unit.plate}</td>
              <td>{unit.type}</td>
              <td>{unit.indicator} km/gal</td>
              <td>{unit.capacity} Q</td>
              <td>{unit.status}</td>
              <td>
                <Button variant="warning" onClick={() => handleEdit(unit)}>
                  Editar
                </Button>
              </td>
            </tr>
          )) : units.map((unit) => (
            <tr key={unit.id}>
              <td>{unit.number}</td>
              <td>{unit.plate}</td>
              <td>{unit.type}</td>
              <td>{unit.indicator} km/gal</td>
              <td>{unit.capacity} Q</td>
              <td>{unit.status}</td>
              <td>
                <Button variant="warning" onClick={() => handleEdit(unit)}>
                  Editar
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>{isEditing ? "Editar Unidad" : "Agregar Unidad"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mb-3">
            <input
              className="form-control"
              name="number"
              value={newUnit.number}
              onChange={handleChange}
              placeholder="Número de Unidad"
            />
          </div>
          <div className="mb-3">
            <input
              className="form-control"
              name="plate"
              value={newUnit.plate}
              onChange={handleChange}
              placeholder="Placa"
            />
          </div>
          <div className="mb-3">
            <input
              className="form-control"
              name="type"
              value={newUnit.type}
              onChange={handleChange}
              placeholder="Tipo de Vehículo"
            />
          </div>
          <div className="mb-3">
            <input
              className="form-control"
              name="indicator"
              value={newUnit.indicator}
              onChange={handleChange}
              placeholder="Kilómetros por Galón"
            />
          </div>
          <div className="mb-3">
            <input
              className="form-control"
              name="capacity"
              value={newUnit.capacity}
              onChange={handleChange}
              placeholder="Capacidad (Monto en Quetzales)"
            />
          </div>
          <div className="mb-3">
            <select
              className="form-control"
              name="status"
              value={newUnit.status}
              onChange={handleChange}
            >
              <option value="ACTIVO">ACTIVO</option>
              <option value="INACTIVO">INACTIVO</option>
              <option value="TALLER">TALLER</option>
            </select>
          </div>
        </Modal.Body>
        <Modal.Footer>
          {isEditing && (
            <Button variant="danger" onClick={handleDelete}>
              Eliminar
            </Button>
          )}
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancelar
          </Button>
          <Button variant="primary" onClick={isEditing ? handleUpdate : addUnit}>
            {isEditing ? "Actualizar" : "Agregar"}
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default UnitsManagement;