import React, { useState, useEffect } from "react";
import { addRecord, getRecords, updateRecord, deleteRecord } from "../../utils/firebaseService";
import { Modal, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const PilotsManagement = () => {
  const [pilots, setPilots] = useState([]);
  const [newPilot, setNewPilot] = useState({ 
    name: "", 
    role: "Piloto", 
    license: "", 
    status: "ACTIVO", 
    startDate: "", 
    endDate: "", 
    comment: "", 
    empresa: "DUPAZA" // Valor por defecto para empresa
  });
  const [editing, setEditing] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [showBajaModal, setShowBajaModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    const fetchPilots = async () => {
      const data = await getRecords("pilots");
      setPilots(data);
    };
    fetchPilots();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewPilot({ ...newPilot, [name]: value });
  };

  const addPilot = async () => {
    if (newPilot.role === "Piloto" && !newPilot.license) {
      alert("El tipo de licencia es obligatorio para los pilotos.");
      return;
    }

    try {
      if (isEditing) {
        await updateRecord("pilots", pilots[editing].id, newPilot);
        const updatedPilots = [...pilots];
        updatedPilots[editing] = newPilot;
        setPilots(updatedPilots);
      } else {
        const pilot = {
          name: newPilot.name,
          role: newPilot.role,
          license: newPilot.license,
          status: "ACTIVO",
          startDate: newPilot.startDate,
          endDate: "",
          comment: "",
          empresa: newPilot.empresa
        };
        const addedPilot = await addRecord("pilots", pilot);
        setPilots([...pilots, { ...pilot, id: addedPilot.id }]);
      }
      resetForm();
    } catch (error) {
      console.error("Error al agregar o actualizar piloto:", error);
      alert("Ocurrió un error al agregar o actualizar al piloto.");
    }
  };

  const resetForm = () => {
    setNewPilot({ 
      name: "", 
      role: "Piloto", 
      license: "", 
      status: "ACTIVO", 
      startDate: "", 
      endDate: "", 
      comment: "", 
      empresa: "DUPAZA" // Valor por defecto para empresa
    });
    setIsEditing(false);
    setShowModal(false);
    setShowBajaModal(false);
  };

  const startEditing = (index) => {
    setIsEditing(true);
    setNewPilot(pilots[index]);
    setEditing(index);
    setShowModal(true);
  };

  const handleDelete = async () => {
    try {
      await deleteRecord("pilots", pilots[editing].id);
      const updatedPilots = pilots.filter((pilot, index) => index !== editing);
      setPilots(updatedPilots);
      resetForm();
    } catch (error) {
      console.error("Error al eliminar piloto:", error);
      alert("Ocurrió un error al eliminar al piloto.");
    }
  };

  const handleBaja = async () => {
    if (window.confirm("¿Estás seguro de dar de baja a este piloto o auxiliar?")) {
      try {
        const updatedPilot = {
          ...pilots[editing],
          status: "INACTIVO",
          endDate: newPilot.endDate,
          comment: newPilot.comment
        };
        await updateRecord("pilots", pilots[editing].id, updatedPilot);
        const updatedPilots = [...pilots];
        updatedPilots[editing] = updatedPilot;
        setPilots(updatedPilots);
        resetForm();
      } catch (error) {
        console.error("Error al dar de baja piloto:", error);
        alert("Ocurrió un error al dar de baja al piloto.");
      }
    }
  };

  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
  };

  const getActiveCount = (role, status) => {
    return pilots.filter(pilot => pilot.role === role && pilot.status === status).length;
  };

  const filteredPilots = pilots.filter(pilot => {
    return pilot.name.toLowerCase().includes(searchQuery.toLowerCase());
  });

  return (
    <div className="container mt-5">
      <h2>Gestión de Pilotos y Auxiliares</h2>

      <h3>Resumen</h3>
      <div className="row">
        <div className="col-md-3">
          <div className="card bg-info text-white">
            <div className="card-body">
              <h5 className="card-title">Pilotos Activos</h5>
              <p className="card-text">{getActiveCount("Piloto", "ACTIVO")}</p>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <div className="card bg-secondary text-white">
            <div className="card-body">
              <h5 className="card-title">Auxiliares Activos</h5>
              <p className="card-text">{getActiveCount("Auxiliar", "ACTIVO")}</p>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <div className="card bg-light border-info">
            <div className="card-body">
              <h5 className="card-title">Pilotos Inactivos</h5>
              <p className="card-text">{getActiveCount("Piloto", "INACTIVO")}</p>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <div className="card bg-light border-secondary">
            <div className="card-body">
              <h5 className="card-title">Auxiliares Inactivos</h5>
              <p className="card-text">{getActiveCount("Auxiliar", "INACTIVO")}</p>
            </div>
          </div>
        </div>
      </div>

      <Button variant="primary" onClick={() => {
        setIsEditing(false);
        resetForm();
        setShowModal(true);
      }}>
        Agregar Piloto o Auxiliar
      </Button>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>{isEditing ? "Editar Piloto o Auxiliar" : "Agregar Piloto o Auxiliar"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mb-3">
            <input
              className="form-control"
              name="name"
              value={newPilot.name}
              onChange={handleChange}
              placeholder="Nombre"
              aria-label="Nombre"
            />
          </div>
          <div className="mb-3">
            <select
              className="form-control"
              name="role"
              value={newPilot.role}
              onChange={handleChange}
              aria-label="Rol"
            >
              <option value="Piloto">Piloto</option>
              <option value="Auxiliar">Auxiliar</option>
            </select>
          </div>
          {newPilot.role === "Piloto" && (
            <div className="mb-3">
              <input
                className="form-control"
                name="license"
                value={newPilot.license}
                onChange={handleChange}
                placeholder="Tipo de Licencia"
                aria-label="Tipo de Licencia"
              />
            </div>
          )}
          {isEditing && newPilot.status === "INACTIVO" && (
            <div className="mb-3">
              <p>Estado: INACTIVO</p>
            </div>
          )}
          <div className="mb-3">
            <label>Fecha de Alta:</label>
            <input
              type="date"
              className="form-control"
              name="startDate"
              value={newPilot.startDate}
              onChange={handleChange}
              aria-label="Fecha de Alta"
            />
          </div>
          <div className="mb-3">
            <label>Empresa:</label>
            <select
              className="form-control"
              name="empresa"
              value={newPilot.empresa}
              onChange={handleChange}
              aria-label="Empresa"
            >
              <option value="DUPAZA">DUPAZA</option>
              <option value="FAMESA">FAMESA</option>
            </select>
          </div>
        </Modal.Body>
        <Modal.Footer>
          {isEditing && newPilot.status === "ACTIVO" && (
            <Button variant="danger" onClick={() => {
              setIsEditing(true);
              setShowBajaModal(true);
            }}>
              Dar de Baja
            </Button>
          )}
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cancelar
          </Button>
          <Button variant="primary" onClick={addPilot}>
            {isEditing ? "Actualizar" : "Agregar"}
          </Button>
        </Modal.Footer>
      </Modal>

      <Modal show={showBajaModal} onHide={() => setShowBajaModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Dar de Baja</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mb-3">
            <label>Motivo de Baja:</label>
            <input
              className="form-control"
              name="comment"
              value={newPilot.comment}
              onChange={handleChange}
              placeholder="Motivo de Baja"
              aria-label="Motivo de Baja"
            />
          </div>
          <div className="mb-3">
            <label>Fecha de Baja:</label>
            <input
              type="date"
              className="form-control"
              name="endDate"
              value={newPilot.endDate}
              onChange={handleChange}
              aria-label="Fecha de Baja"
            />
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowBajaModal(false)}>
            Cancelar
          </Button>
          <Button variant="primary" onClick={handleBaja}>
            Dar de Baja
          </Button>
        </Modal.Footer>
      </Modal>

      <h3 className="mt-3">Pilotos y Auxiliares</h3>
      <div className="mb-3">
        <input
          type="text"
          className="form-control"
          value={searchQuery}
          onChange={handleSearch}
          placeholder="Buscar por nombre"
          aria-label="Buscar por nombre"
        />
      </div>
      <table className="table table-bordered">
        <thead className="thead-dark">
          <tr>
            <th>Nombre</th>
            <th>Rol</th>
            <th>Licencia</th>
            <th>Estado</th>
            <th>Empresa</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {filteredPilots.map((pilot, index) => (
            <tr key={index}>
              <td>{pilot.name}</td>
              <td>{pilot.role}</td>
              <td>{pilot.license}</td>
              <td>{pilot.status}</td>
              <td>{pilot.empresa}</td>
              <td>
                <Button variant="warning" onClick={() => startEditing(index)}>
                  Editar
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default PilotsManagement;