import React, { useState, useEffect } from 'react';
import { db } from '../FirebaseConfig';
import { collection, getDocs } from 'firebase/firestore';

const TripLog = () => {
  const [registros, setRegistros] = useState([]);
  const [empresas, setEmpresas] = useState({});
  const [fechaFiltro, setFechaFiltro] = useState(() => {
    const fechaActual = new Date();
    const dia = fechaActual.getDate().toString().padStart(2, '0');
    const mes = (fechaActual.getMonth() + 1).toString().padStart(2, '0');
    const año = fechaActual.getFullYear();
    return `${año}-${mes}-${dia}`;
  });
  const [refrescar, setRefrescar] = useState(false);

  useEffect(() => {
    const obtenerRegistros = async () => {
      const salidasRef = collection(db, 'salidaretorno');
      const salidasSnap = await getDocs(salidasRef);
      const salidasData = salidasSnap.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setRegistros(salidasData);
      const empresasObj = {};
      salidasData.forEach((registro) => {
        if (!empresasObj[registro.empresa]) {
          empresasObj[registro.empresa] = {
            enRuta: [],
            retornados: [],
          };
        }
        if (!registro.fechaRetorno && !registro.horaRetorno) {
          empresasObj[registro.empresa].enRuta.push(registro);
        } else {
          empresasObj[registro.empresa].retornados.push(registro);
        }
      });
      setEmpresas(empresasObj);
    };
    obtenerRegistros();
  }, [refrescar]);

  const handleActualizarRegistros = () => {
    setRefrescar(true);
  };

  const handleFiltrar = (e) => {
    setFechaFiltro(e.target.value);
  };

  const fechasUnicas = [...new Set(registros.map((registro) => registro.fechaSalida))];

  return (
    <div className="container mt-5">
      <h2>Registro de Salidas y Retornos</h2>
      <button className="btn btn-primary" onClick={handleActualizarRegistros}>
        Actualizar Registros
      </button>
      <select value={fechaFiltro} onChange={handleFiltrar}>
        <option value="">Todos</option>
        {fechasUnicas.map((fecha, index) => (
          <option key={index} value={fecha}>{fecha}</option>
        ))}
      </select>
      {Object.keys(empresas).map((empresa) => (
        <div key={empresa} className="row" style={{
          backgroundColor: empresa === 'FAMESA' ? '#ADD8E6' : '#FFA07A',
          padding: '20px',
          marginBottom: '20px',
          borderRadius: '10px',
          boxShadow: '0 0 10px rgba(0, 0, 0, 0.1)',
        }}>
          <h3 style={{ color: '#333' }}>{empresa}</h3>
          <h4>En ruta:</h4>
          <table className="table table-striped">
            <thead>
              <tr>
                <th>Unidad No.</th>
                <th>Placa</th>
                <th>Tipo Unidad</th>
                <th>Piloto</th>
                <th>Auxiliar</th>
                <th>Ruta</th>
                <th>Fecha Salida</th>
                <th>Hora Salida</th>
                <th>Kilometro Salida</th>
              </tr>
            </thead>
            <tbody>
              {empresas[empresa].enRuta.filter((registro) => {
                if (fechaFiltro === "") return true;
                return registro.fechaSalida === fechaFiltro;
              }).map((registro, index) => (
                <tr key={index}>
                  <td>{registro.numeroUnidad}</td>
                  <td>{registro.placa || 'N/A'}</td>
                  <td>{registro.tipoUnidad || 'N/A'}</td>
                  <td>{registro.piloto || 'N/A'}</td>
                  <td>{registro.auxiliar || 'N/A'}</td>
                  <td>{registro.ruta || 'N/A'}</td>
                  <td>{registro.fechaSalida || 'N/A'}</td>
                  <td>{registro.horaSalida || 'N/A'}</td>
                  <td>{registro.kilometroSalida || 'N/A'}</td>
                </tr>
              ))}
            </tbody>
          </table>
          <h4>Retornados:</h4>
          <table className="table table-striped">
            <thead>
              <tr>
                <th>Unidad No.</th>
                <th>Placa</th>
                <th>Tipo Unidad</th>
                <th>Piloto</th>
                <th>Auxiliar</th>
                <th>Ruta</th>
                <th>Fecha Salida</th>
                <th>Hora Salida</th>
                <th>Kilometro Salida</th>
                <th>Fecha Retorno</th>
                <th>Hora Retorno</th>
                <th>Kilometro Retorno</th>
              </tr>
            </thead>
            <tbody>
              {empresas[empresa].retornados.filter((registro) => {
                if (fechaFiltro === "") return true;
                return registro.fechaSalida === fechaFiltro;
              }).map((registro, index) => (
                <tr key={index}>
                  <td>{registro.numeroUnidad}</td>
                  <td>{registro.placa || 'N/A'}</td>
                  <td>{registro.tipoUnidad || 'N/A'}</td>
                  <td>{registro.piloto || 'N/A'}</td>
                  <td>{registro.auxiliar || 'N/A'}</td>
                  <td>{registro.ruta || 'N/A'}</td>
                  <td>{registro.fechaSalida || 'N/A'}</td>
                  <td>{registro.horaSalida || 'N/A'}</td>
                  <td>{registro.kilometroSalida || 'N/A'}</td>
                  <td>{registro.fechaRetorno || 'N/A'}</td>
                  <td>{registro.horaRetorno || 'N/A'}</td>
                  <td>{registro.kilometroRetorno || 'N/A'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ))}
    </div>
  );
};

export default TripLog;