import React, { useState, useEffect } from 'react';
import { getFirestore, collection, getDocs, query, where, addDoc, updateDoc, doc, setDoc } from 'firebase/firestore';
import { db } from './FirebaseConfig';
import { Table, Button, Form, Row, Col, Modal } from 'react-bootstrap';

const ControlRecibos = () => {
  const [SalidaRetorno, setSalidaRetorno] = useState([]);
  const [filtroFecha, setFiltroFecha] = useState('');
  const [fechas, setFechas] = useState([]);
  const [formValues, setFormValues] = useState({});
  const [comprobante, setComprobante] = useState(null);
  const [mostrarComprobante, setMostrarComprobante] = useState(false)

  useEffect(() => {
    const obtenerReportes = async () => {
      const reportesRef = collection(db, 'salidaretorno');
      const reportesSnap = await getDocs(reportesRef);
      const reportesData = reportesSnap.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setSalidaRetorno(reportesData);
    };
    obtenerReportes();
  }, []);

  useEffect(() => {
    const obtenerFechas = async () => {
      const reportesRef = collection(db, 'salidaretorno');
      const reportesSnap = await getDocs(reportesRef);
      const fechasData = [...new Set(reportesSnap.docs.map((doc) => doc.data().fechaSalida))];
      setFechas(fechasData);
    };
    obtenerFechas();
  }, []);

  const handleFiltroFechaChange = async (e) => {
    const filtroFecha = e.target.value;
    if (filtroFecha === "") {
      const reportesRef = collection(db, 'salidaretorno');
      const reportesSnap = await getDocs(reportesRef);
      const reportesData = reportesSnap.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setSalidaRetorno(reportesData);
    } else {
      const reportesRef = collection(db, 'salidaretorno');
      const q = query(reportesRef, where('fechaSalida', '==', filtroFecha));
      const reportesSnap = await getDocs(q);
      const reportesData = reportesSnap.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      setSalidaRetorno(reportesData);
    }
  };

  const handleGuardarRecibo = async (report) => {
    const reportesRef = collection(db, 'salidaretorno');
    const reporteDoc = doc(reportesRef, report.id);
    const updateData = {
      ...report,
    };
    await setDoc(reporteDoc, updateData, { merge: true });
    const nuevoSalidaRetorno = SalidaRetorno.map((item) => {
      if (item.id === report.id) {
        return { ...item, ...report };
      }
      return item;
    });
    setSalidaRetorno(nuevoSalidaRetorno);
  };

  const verComprobante = (refDeposito) => {
    const apiKey = 'AIzaSyC6JfLVqYrWQrQhL2IxbxzW91b3p7eR3NA';
    const folderId = '1i1wCpd2VoppciNo8YuCO2W5-tPIfW8h_';
    const url = `https://www.googleapis.com/drive/v3/files?q='${folderId}' in parents and name='${refDeposito}.png'&key=${apiKey}`;
    fetch(url)
      .then(response => response.json())
      .then(data => {
        if (data.files.length > 0) {
          const fileId = data.files[0].id;
          const comprobanteUrl = `https://www.googleapis.com/drive/v3/files/${fileId}?key=${apiKey}&alt=media`;
          setComprobante(comprobanteUrl);
          setMostrarComprobante(true);
        } else {
          console.error('No se encontró la imagen');
        }
      })
      .catch(error => console.error(error));
  };

  const cerrarComprobante = () => {
    setMostrarComprobante(false);
  };

  return (
    <div>
      <h2>Control Recibos</h2>
      <Row>
        <Col>
          <Form>
            <Form.Group controlId="fechaSalida">
              <Form.Label>Fecha de Salida</Form.Label>
              <Form.Select value={filtroFecha} onChange={handleFiltroFechaChange}>
                <option value="">Todos</option>
                {fechas.map(fecha => (
                  <option key={fecha} value={fecha}>{fecha}</option>
                ))}
              </Form.Select>
            </Form.Group>
          </Form>
        </Col>
      </Row>
      <h2>Pendientes de Recibo</h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Empresa</th>
            <th>Fecha de Salida</th>
            <th>Hora de Salida</th>
            <th>No. Unidad</th>
            <th>Placa</th>
            <th>Ruta</th>
            <th>Piloto</th>
            <th>Auxiliar</th>
            <th>Monto</th>
            <th>Ref. Depósito</th>
            <th>Tipo de Acrediracion</th>
            <th>Acreditar</th>
          </tr>
        </thead>
        <tbody>
          {SalidaRetorno.filter(report => !report.monto || !report.refDeposito).map(report => (
            <tr key={report.id}>
              <td>{report.empresa}</td>
              <td>{report.fechaSalida}</td>
              <td>{report.horaSalida}</td>
              <td>{report.numeroUnidad}</td>
              <td>{report.placa}</td>
              <td>{report.ruta}</td>
              <td>{report.piloto}</td>
              <td>{report.auxiliar}</td>
              <td>
                <input type="text" name="monto" value={formValues[report.id] && formValues[report.id].monto} onChange={(e) => {
                  setFormValues({
                    ...formValues,
                    [report.id]: {
                      ...formValues[report.id],
                      monto: e.target.value
                    }
                  });
                }} placeholder="Monto" />
              </td>
              <td>
                <input type="text" name="refDeposito" value={formValues[report.id] && formValues[report.id].refDeposito} onChange={(e) => {
                  setFormValues({
                    ...formValues,
                    [report.id]: {
                      ...formValues[report.id],
                      refDeposito: e.target.value
                    }
                  });
                }} placeholder="Referencia de Depósito" />
              </td>
              <td>
                <input type="text" name="refAcreditacion" value={formValues[report.id] && formValues[report.id].refAcreditacion} onChange={(e) => {
                  setFormValues({
                    ...formValues,
                    [report.id]: {
                      ...formValues[report.id],
                      refAcreditacion: e.target.value
                    }
                  });
                }} placeholder="Ref Acreditacion" />
              </td>
              <td>
                <Button onClick={() => handleGuardarRecibo({ 
                  ...report, 
                  monto: formValues[report.id] && formValues[report.id].monto, 
                  refDeposito: formValues[report.id] && formValues[report.id].refDeposito, 
                  refAcreditacion: formValues[report.id] && formValues[report.id].refAcreditacion, 
                  firma: '' 
                })}>Acreditar</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <h2>Recibos Operados</h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Empresa</th>
            <th>Fecha de Salida</th>
            
            <th>No. Unidad</th>
            <th>Piloto</th>
            <th>Auxiliar</th>
       
            <th>Ref de Depósito</th>
            <th>Monto</th>
            <th>Tipo de Acred.</th>
            <th>Firma</th>
            <th>Ver Comprobante</th>
          </tr>
        </thead>
        <tbody>
          {SalidaRetorno.filter(report => report.monto && report.refDeposito).map(report => (
            <tr key={report.id}>
              <td>{report.empresa}</td>
              <td>{report.fechaSalida}</td>
             
              <td>{report.numeroUnidad}</td>
              <td>{report.piloto}</td>
              <td>{report.auxiliar}</td>
             
              <td>{report.refDeposito}</td>
              <td>{report.monto}</td>
              <td>{report.refAcreditacion}</td>
              <img src={report.firma} alt="Firma" style={{ maxWidth: '25%', maxHeight: '10%' }} />
              <td>
                <Button onClick={() => verComprobante(report.refDeposito)}>Ver Comprobante</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      {mostrarComprobante && (
        <div style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', backgroundColor: 'rgba(0, 0, 0, 0.5)', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <img src={comprobante} alt="Comprobante" style={{ maxWidth: '80%', maxHeight: '80%' }} />
          <Button onClick={cerrarComprobante} style={{ position: 'absolute', top: 10, right: 10 }}>Cerrar</Button>
        </div>
      )}
    </div>
  );
};

export default ControlRecibos;