import React, { useState, useEffect } from 'react';
import { getFirestore, collection, getDocs, query, where, addDoc, updateDoc, doc } from 'firebase/firestore';
import { db } from '../FirebaseConfig';
import { Container, Row, Col, Table, Modal, Form, Button } from 'react-bootstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { format, parse } from 'date-fns';
import es from 'date-fns/locale/es'; // Para manejar formatos en español

const FuelRequest = () => {
  const [solicitudes, setSolicitudes] = useState([]);
  const [solicitudesAtendidas, setSolicitudesAtendidas] = useState([]);
  const [solicitudesConDeposito, setSolicitudesConDeposito] = useState([]);
  const [kilometros, setKilometros] = useState({});
  const [refDeposito, setRefDeposito] = useState({});
  const [showModal, setShowModal] = useState(false);
  const [modalData, setModalData] = useState({});
  const [recalcularModal, setRecalcularModal] = useState(false);
  const [recalcularId, setRecalcularId] = useState(null);
  const [recalcularKilometros, setRecalcularKilometros] = useState('');

  const obtenerSolicitudes = async () => {
    const solicitudesRef = collection(db, 'solicitudesCombustible');
    const solicitudesSnap = await getDocs(solicitudesRef);
    const solicitudesData = solicitudesSnap.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
    setSolicitudes(solicitudesData.filter((solicitud) => !solicitud.montoAcreditar));
    setSolicitudesAtendidas(solicitudesData.filter((solicitud) => solicitud.montoAcreditar && !solicitud.refDeposito));
    setSolicitudesConDeposito(solicitudesData.filter((solicitud) => solicitud.refDeposito));
  };

  useEffect(() => {
    obtenerSolicitudes();
  }, []);

  const calcularCombustible = (id, indicador, precioCombustible) => {
    const kilometrosValor = kilometros[id];
    if (kilometrosValor !== undefined && kilometrosValor !== null) {
      const montoAcreditar = Math.ceil((kilometrosValor / indicador) * precioCombustible);
      const montoAcreditarRedondeado = Math.ceil(montoAcreditar / 5) * 5;
      const solicitudesRef = doc(db, 'solicitudesCombustible', id);
      updateDoc(solicitudesRef, {
        kilometros: kilometrosValor,
        montoAcreditar: montoAcreditarRedondeado,
      })
        .then(() => {
          console.log('Combustible calculado con éxito');
          obtenerSolicitudes();
        })
        .catch((error) => {
          console.error('Error al calcular combustible', error);
        });
    } else {
      console.error('El campo de kilometros es requerido');
    }
  };

  const handleKilometrosChange = (id, value) => {
    setKilometros((prevKilometros) => ({ ...prevKilometros, [id]: value }));
  };

  const handleDepositoChange = (id, value) => {
    setRefDeposito((prevRefDeposito) => ({ ...prevRefDeposito, [id]: value }));
  };

  const recalcularCombustible = (id, indicador, precioCombustible) => {
    const kilometrosValor = recalcularKilometros;
    if (kilometrosValor !== undefined && kilometrosValor !== null) {
      const montoAcreditar = Math.ceil((kilometrosValor / indicador) * precioCombustible);
      const montoAcreditarRedondeado = Math.ceil(montoAcreditar / 5) * 5;
      const solicitudesRef = doc(db, 'solicitudesCombustible', id);
      updateDoc(solicitudesRef, {
        kilometros: kilometrosValor,
        montoAcreditar: montoAcreditarRedondeado,
      })
        .then(() => {
          console.log('Combustible recalculado con éxito');
          obtenerSolicitudes();
          setRecalcularModal(false);
        })
        .catch((error) => {
          console.error('Error al recalcular combustible', error);
        });
    } else {
      console.error('El campo de kilometros es requerido');
    }
  };

  const guardarReferenciaDeposito = (id) => {
    const refDepositoValor = refDeposito[id];
    if (refDepositoValor !== undefined && refDepositoValor !== null) {
      const solicitudesRef = doc(db, 'solicitudesCombustible', id);
      updateDoc(solicitudesRef, {
        refDeposito: refDepositoValor,
      })
        .then(() => {
          console.log('Referencia de depósito guardada con éxito');
          obtenerSolicitudes();
        })
        .catch((error) => {
          console.error('Error al guardar la referencia de depósito', error);
        });
    } else {
      console.error('El campo de referencia de depósito es requerido');
    }
  };

  const acreditarSolicitud = (id) => {
    const solicitudesRef = doc(db, 'solicitudesCombustible', id);
    updateDoc(solicitudesRef, {
      refDeposito: refDeposito[id],
    })
      .then(() => {
        console.log('Solicitud acreditada con éxito');
        obtenerSolicitudes();
      })
      .catch((error) => {
        console.error('Error al acreditar solicitud', error);
      });
  };

  const handleShowModal = (id, indicador, precioCombustible, kilometrosValor, montoAcreditarRedondeado, fechaYHora) => {
    const solicitud = solicitudesAtendidas.find((solicitud) => solicitud.id === id);
    if (solicitud) {
      const montoAcreditar = Math.ceil((kilometrosValor / indicador) * precioCombustible);
      const montoAcreditarRedondeado = Math.ceil(montoAcreditar / 5) * 5;
      setModalData({
        id,
        indicador,
        precioCombustible,
        kilometrosValor,
        montoAcreditarRedondeado,
        unidad: solicitud.unidad,
        placa: solicitud.placa,
        fechaYHora,
        piloto: solicitud.piloto,
        ruta: solicitud.ruta,
        kilometrage: solicitud.kilometrage,
        empresa: solicitud.empresa,
      });
    }
    setShowModal(true);
  };

  const handleRefrescar = () => {
    obtenerSolicitudes();
  };

  const handleRecalcularModal = (id) => {
    const solicitud = solicitudesAtendidas.find((solicitud) => solicitud.id === id);
    if (solicitud) {
      setRecalcularId(id);
      setRecalcularKilometros(solicitud.kilometros.toString());
    }
    setRecalcularModal(true);
  };




  return (
    <Container>
      <Row>
        <Col md={12}>
          <h2>Solicitudes de Combustible</h2>
          {/* Elimina el DatePicker */}
          {/* <div className="mb-3">
            <label>Fecha:</label>
            <DatePicker
              selected={selectedDate}
              onChange={handleDateChange}
              dateFormat="dd/MM/yyyy"
              className="form-control"
            />
          </div> */}
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Empresa</th>
                <th>Unidad</th>
                <th>Placa</th>
                <th>Ruta</th>
                <th>Piloto</th>
                <th>Kilometrage</th>
                <th>Kilometros</th>
                <th>Calcular Combustible</th>
              </tr>
            </thead>
            <tbody>
              {solicitudes.map((solicitud) => (
                <tr key={solicitud.id}>
                  <td>{solicitud.fechaYHora}</td>
                  <td>{solicitud.empresa}</td>
                  <td>{solicitud.unidad}</td>
                  <td>{solicitud.placa}</td>
                  <td>{solicitud.ruta}</td>
                  <td>{solicitud.piloto}</td>
                  <td>{solicitud.kilometrage}</td>
                  <td>
                    <input
                      type="number"
                      value={kilometros[solicitud.id] || solicitud.kilometros}
                      onChange={(event) => handleKilometrosChange(solicitud.id, event.target.value)}
                    />
                  </td>
                  <td>
                    <Button
                      variant="primary"
                      onClick={() => {
                        if (kilometros[solicitud.id] !== undefined && kilometros[solicitud.id] !== null) {
                          calcularCombustible(solicitud.id, solicitud.indicador, solicitud.precioCombustible);
                        } else {
                          console.error('El campo de kilometros es requerido');
                        }
                      }}
                    >
                      Calcular Combustible
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
          <Button variant="primary" onClick={handleRefrescar}>
            Refrescar
          </Button>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
          <h2>Solicitudes Atendidas</h2>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Empresa</th>
                <th>Unidad</th>
                <th>Placa</th>
                <th>Ruta</th>
                <th>Piloto</th>
                <th>Kilometros</th>
                <th>Kilometrage</th>
                <th>Monto a Acreditar</th>
                <th>Ver Detalles</th>
                <th>Recalcular</th>
                <th>Ref. Depósito</th>
                <th>Acreditar</th>
              </tr>
            </thead>
            <tbody>
              {solicitudesAtendidas.map((solicitud) => (
                <tr key={solicitud.id}>
                  <td>{solicitud.fechaYHora}</td>
                  <td>{solicitud.empresa}</td>
                  <td>{solicitud.unidad}</td>
                  <td>{solicitud.placa}</td>
                  <td>{solicitud.ruta}</td>
                  <td>{solicitud.piloto}</td>
                  <td>{solicitud.kilometros}</td>
                  <td>{solicitud.kilometrage}</td>
                  <td>{solicitud.montoAcreditar}</td>
                  <td>
                    <Button
                      variant="primary"
                      onClick={() => {
                        const kilometrosValor = solicitud.kilometros;
                        const montoAcreditarRedondeado = solicitud.montoAcreditar;
                        handleShowModal(solicitud.id, solicitud.indicador, solicitud.precioCombustible, kilometrosValor, montoAcreditarRedondeado, solicitud.fechaYHora);
                      }}
                    >
                      Ver Detalles
                    </Button>
                  </td>
                  <td>
                    <Button variant="primary" onClick={() => handleRecalcularModal(solicitud.id)}>
                      Recalcular
                    </Button>
                  </td>
                  <td>
                    <input
                      type="text"
                      value={refDeposito[solicitud.id] || solicitud.refDeposito}
                      onChange={(event) => handleDepositoChange(solicitud.id, event.target.value)}
                    />
                  </td>
                  <td>
                    <Button variant="primary" onClick={() => acreditarSolicitud(solicitud.id)}>
                      Acreditar
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
          <h2>Solicitudes con Depósito</h2>
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Empresa</th>
                <th>Unidad</th>
                <th>Placa</th>
                <th>Ruta</th>
                <th>Piloto</th>
                <th>Kilometros</th>
                <th>Kilometrage</th>
                <th>Monto a Acreditar</th>
                <th>Referencia de Depósito</th>
              </tr>
            </thead>
            <tbody>
              {solicitudesConDeposito.map((solicitud) => (
                <tr key={solicitud.id}>
                  <td>{solicitud.fechaYHora}</td>
                  <td>{solicitud.empresa}</td>
                  <td>{solicitud.unidad}</td>
                  <td>{solicitud.placa}</td>
                  <td>{solicitud.ruta}</td>
                  <td>{solicitud.piloto}</td>
                  <td>{solicitud.kilometros}</td>
                  <td>{solicitud.kilometrage}</td>
                  <td>{solicitud.montoAcreditar}</td>
                  <td>{solicitud.refDeposito}</td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Col>
      </Row>
      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>CALCULO DE COMBUSTIBLE</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Kilometros: {modalData.kilometrosValor} Kms.
          <br />
          Unidad: {modalData.unidad}
          <br />
          Placa: {modalData.placa}
          <br />
          Galones requeridos: {Math.ceil(modalData.kilometrosValor / modalData.indicador)} galones
          <br />
          Precio Combustible: Q {modalData.precioCombustible}
          <br />
          Fecha: {modalData.fechaYHora}
          <br />
          Piloto: {modalData.piloto}
          <br />
          Ruta: {modalData.ruta}
          <br />
          Empresa: {modalData.empresa}
          <br />
          <p style={{ fontSize: 24, fontWeight: 'bold' }}>Monto a Acreditar: Q {modalData.montoAcreditarRedondeado}</p>
          <br />
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            Cerrar
          </Button>
        </Modal.Footer>
      </Modal>
      <Modal show={recalcularModal} onHide={() => setRecalcularModal(false)}>
        <Modal.Header closeButton>
      


          <Modal.Title>Recalcular Combustible</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId="formBasicKilometros">
              <Form.Label>Kilometros</Form.Label>
              <Form.Control
               
                type="number"
                value={recalcularKilometros}
                onChange={(event) => setRecalcularKilometros(event.target.value)}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setRecalcularModal(false)}>
            Cancelar
          </Button>
          <Button variant="primary" onClick={() => recalcularCombustible(recalcularId, modalData.indicador, modalData.precioCombustible)}>
            Aceptar
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default FuelRequest;